$black: black;
$white: white;
$green : green;
$animation: glow 1s ease-in-out infinite alternate;

.banner{
    .alert{
        background: $black;
        padding: 8px ;
        margin: 0;
        width: 100%;
        border-radius: none;
       
       

    }
    .message-alert{
        .glow{
            color: #fff;
            text-shadow: 0 0 4px #00b3ff,0 0 5px #00b3ff,0 0 6px #00b3ff,0 0 7px #00b3ff,0 0 8px #00b3ff;
                &:last-child span{
                    color: white;
                    text-shadow: 0 0 4px rgb(240, 76, 76),0 0 5px rgb(240, 76, 76),0 0 6px rgb(240, 76, 76),0 0 7px rgb(236, 16, 16),0 0 8px rgb(240, 76, 76);
                }
            }
        .orange{
                border-radius: none;
                color: white;
                text-shadow: 0 0 5px rgb(219, 152, 51),0 0 6px rgb(247, 136, 45),0 0 7px rgb(196, 63, 23),0 0 8px rgb(219, 152, 51),0 0 9px rgb(219, 152, 51);
                &:last-child span{
                    color: white;
                    text-shadow: 0 0 4px rgb(240, 76, 76),0 0 5px rgb(240, 76, 76),0 0 6px rgb(240, 76, 76),0 0 7px rgb(240, 76, 76),0 0 8px rgb(240, 76, 76);
                }  
            }
        }
}

