@import url(https://fonts.googleapis.com/css2?family=Baloo+Tamma+2&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


 .App {
  text-align: center;
  
}
body{
  font-family: 'Baloo Tamma 2', cursive;
}
.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.banner .alert{background:#000;padding:8px;margin:0;width:100%;border-radius:none}.banner .message-alert .glow{color:#fff;text-shadow:0 0 4px #00b3ff,0 0 5px #00b3ff,0 0 6px #00b3ff,0 0 7px #00b3ff,0 0 8px #00b3ff}.banner .message-alert .glow:last-child span{color:white;text-shadow:0 0 4px #f04c4c,0 0 5px #f04c4c,0 0 6px #f04c4c,0 0 7px #ec1010,0 0 8px #f04c4c}.banner .message-alert .orange{border-radius:none;color:white;text-shadow:0 0 5px #db9833,0 0 6px #f7882d,0 0 7px #c43f17,0 0 8px #db9833,0 0 9px #db9833}.banner .message-alert .orange:last-child span{color:white;text-shadow:0 0 4px #f04c4c,0 0 5px #f04c4c,0 0 6px #f04c4c,0 0 7px #f04c4c,0 0 8px #f04c4c}


@media (min-width:320px)and (max-width:480px)
{
    .alertion{
        flex-direction: column;
        justify-content: center;
        align-items: center;
        }
    .orange{      
        padding: 2px;
    }
    .alertion p{
        font-size: 10px !important;
    }
    .orange:first-child{
        width: 295px;
    }
}

@media (min-width:481px)and (max-width:768px){
    .orange{
    display: flex ;
    font-size: 14px !important;
    width: 312px;
    border: 2px solid red;
    justify-content: flex-end;
    }
    .orange:last-child {
        width: 240px;
    }
}
